<template>
    <div class="service-item">
        <component 
            :is="iconComponent"
            class="service-item__icon"
        />

        <div class="service-item__text">
            <span class="service-item__title">
                {{ serviceName }}
            </span>
            <span class="service-item__price">
                <span>
                    {{ servicePrice }}&nbsp;&#8381;/{{ subscriptionPeriod }}
                </span>
            </span>
        </div>
    </div>
</template>

<script>
    import { defineAsyncComponent, defineComponent } from "vue";
    import { editServiceName } from "./helper";

    export default defineComponent({
        name: "ServiceItem",
        components: {
            DomofonIcon: defineAsyncComponent(() => import("@/components/icons/DomofonIcon.vue")),
            CameraIcon: defineAsyncComponent(() => import("@/components/icons/CameraIcon.vue")),
        },
        props: {
            service: {
                type: Object,
                default: () => {},
            },
            subscribeDuration: {
                type: Number,
                default: 1,
            }
        },
        computed: {
            subscriptionPeriod() {
                return this.subscribeDuration === 12 ? "год" : "мес";
            },
            servicePrice() {
                return this.service?.PRICE[this.subscribeDuration] || "?";
            },
            serviceName() {
                return editServiceName(this.service?.SERVICE_NAME) || "Имя сервиса";
            },
            iconComponent() {
                return this.serviceName === "Умный домофон" ? "DomofonIcon" : "CameraIcon";
            },
        }
    });
</script>

<style scoped>
.service-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-content: flex-start;
}

.service-item__icon {
  width: 20px;
  height: 20px;
  margin-right: 18px;
  margin-top: 2px;
  color: #1274FF;
}

.service-item__text {
  display: flex;
  flex-direction: column;
}

.service-item__title {
  display: flex;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.02em;
  margin-bottom: 4px;
}

.service-item__price {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: #9B9FA9;
}
</style>